import React, {useState, useEffect} from 'react';
import axios from 'axios';

function Categories() {
  const [category, setCategory] = useState([]);

  useEffect(() => {
    axios({
      method: "GET",
      url: "http://127.0.0.1:8000/api/category/"
    }).then(response => {
      setCategory(response.data)
    })
  }, [])
  return (
    <div className="App">
      {setCategory.map(c => (
        <h3>c</h3>
      ))}
    </div>
  );
}

export default Categories;
